import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { TweenMax, TimelineMax, Linear } from 'gsap';
import * as ScrollMagic from 'scrollmagic';
import * as $ from 'jquery';
import 'imports-loader?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  @Output()
  pageChanged: EventEmitter<String> = new EventEmitter<String>();
  magicCtrl: any;

  constructor() { }

  change(evant: EventEmitter<string>, p: string) {
    this.pageChanged.emit(p);
  }

  ngOnInit() {
    this.setHomeAnim();
  }

  setHomeAnim() {
    var that = this;

    this.magicCtrl = new ScrollMagic.Controller();

    // EHI Header Animation
    var homeHeader = new ScrollMagic.Scene({
      triggerElement: "#ehiHeader",
      triggerHook: .9
    })
      .setTween(TweenMax.from("#ehiHeader", 1, { y: -25, opacity: 0, ease: Linear.easeNone }))
      .addTo(this.magicCtrl);

    // EHI Content Animation
    $(".ehiInfoBlock").each(function (i) {
      var tlContent = new TimelineMax();

      var contentString = "#ehiContent" + (i + 1);
      var picString = "#ehiPic" + (i + 1);

      tlContent.add(TweenMax.from(contentString, 1, { css: "background-size: 0% .5rem; opacity: 0;", ease: Linear.easeNone }));
      tlContent.add(TweenMax.from(picString, 1, { y: -25, opacity: 0, ease: Linear.easeNone }), 0);

      var homeContent = new ScrollMagic.Scene({
        triggerElement: this,
        triggerHook: .75
      })
        .setTween(tlContent)
        .addTo(that.magicCtrl);
    });

    // EHI Footer Animation
    var tlFooter = new TimelineMax();

    tlFooter.add(TweenMax.from("#ehiFooterContain", .25, { x: "-200%", ease: Linear.easeNone }));
    tlFooter.add(TweenMax.to("#ehiFooter", 1, { css: "opacity: 1;", ease: Linear.easeNone }), "-=.1");
    tlFooter.add(TweenMax.to("#ehiFooter", 1, { css: "background-size: 100% 1rem;", ease: Linear.easeNone }), "-=.1");

    var ehiFootScene = new ScrollMagic.Scene({
      triggerElement: "#ehiFooter",
      triggerHook: .5
    })
      .setTween(tlFooter)
      .addTo(this.magicCtrl);

    // ME Header Animation
    var tlMeHeader = new TimelineMax();

    tlMeHeader.add(TweenMax.to(".revealDiv", .1, { css: "width: 100%;", ease: Linear.easeNone }));
    tlMeHeader.add(TweenMax.from(".meHeader", .5, { height: 0, ease: Linear.easeNone }), "reveal");
    tlMeHeader.add(TweenMax.to(".revealDiv", .5, { bottom: 0, top: "auto", ease: Linear.easeNone }), "reveal");
    tlMeHeader.add(TweenMax.to(".revealDiv", .5, { css: "width: 0%;", ease: Linear.easeNone }));

    var homeHeader = new ScrollMagic.Scene({
      triggerElement: "#me",
      triggerHook: .75
    })
      .setTween(tlMeHeader)
      .addTo(this.magicCtrl);

    // ME Content Animations
    var meContentScene = new ScrollMagic.Scene({
      triggerElement: "#metext",
      triggerHook: .7
    })
      .setTween(TweenMax.to("#metext", 1, { opacity: 1, ease: Linear.easeNone }))
      .addTo(this.magicCtrl);

    // ME Footer Animations
    var meFooterScene = new ScrollMagic.Scene({
      triggerElement: "#ehicontact",
      triggerHook: .98
    })
      .setTween(TweenMax.from("#ehicontact", 1, { opacity: 0, ease: Linear.easeNone }))
      .addTo(this.magicCtrl);
  }
}
