import { Component, OnInit } from '@angular/core';
import { TweenMax, TimelineMax, Linear } from 'gsap';
import { Chart } from 'chart.js';
import * as ScrollMagic from 'scrollmagic';
import * as $ from 'jquery';
import 'imports-loader?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {
  magicCtrl: any;
  slide: number;
  clicked: boolean[];
  switches: boolean[];

  constructor() { }

  ngOnInit() {
    this.slide = 1;
    this.clicked = [false, false, false, false];
    this.switches = [false, false, false, false];

    this.setAboutAnim();
  }

  // Slide manipulation
  slideClick(num: number) {
    this.clicked[num - 1] = true;
    this.switches[num - 1] = !this.switches[num - 1];
  }
  plusSlides(num:number) {
    this.slide += num;

    if (this.slide == 5) {
      this.slide = 1;
    }
    if (this.slide == 0) {
      this.slide = 4;
    }
  }
  currentSlide(num:number) {
    this.slide = num;
  }

  isSlide(num: number): boolean {
    if (this.slide === num)
      return true;

    return false;
  }

  isClicked(num: number): boolean {
    return this.clicked[num - 1];
  }

  switch(num:number) {
    return this.switches[num - 1];
  }
  // end of slide manpulation

  // Scrolling animations
  setAboutAnim() {
    var that = this;
    this.magicCtrl = new ScrollMagic.Controller();

    var aboutHeaderTL = new TimelineMax();
    aboutHeaderTL.add(TweenMax.from("#aboutPic", 1, { x: "-200%", opacity: 0, ease: Linear.easeNone }), 0);

    $(".aboutInfoClass").each(function (index) {
      var aboutInfo = TweenMax.from(this, .5, { y: -25, opacity: 0, ease: Linear.easeNone });

      if (index != 0) {
        aboutHeaderTL.add(aboutInfo, "-=.2");
      }
      else {
        aboutHeaderTL.add(aboutInfo, 0);
      }
    });
    var aboutHeaderSC = new ScrollMagic.Scene({
      triggerElement: "#content",
      triggerHook: .3
    })
      .setTween(aboutHeaderTL)
      .addTo(this.magicCtrl);

    // About Description Animation
    var aboutDescTL = new TimelineMax();
    aboutDescTL.add(TweenMax.from("#descHead", 1, { y: -100, opacity: 0, ease: Linear.easeNone }));
    aboutDescTL.add(TweenMax.from("#aboutContent", 1, { opacity: 0, x: -100, ease: Linear.easeNone }), 0);

    var aboutDescSC = new ScrollMagic.Scene({
      triggerElement: "#aboutContent",
      triggerHook: .7
    })
      .setTween(aboutDescTL)
      .addTo(this.magicCtrl);

    // About Attributes Animation
    var aboutAttribTL = new TimelineMax();

    aboutAttribTL.add(TweenMax.from("#attrHead", 1, { y: -100, opacity: 0, ease: Linear.easeNone }));
    aboutAttribTL.add(TweenMax.from("#aboutGraphs", 1, { opacity: 0, x: -100, ease: Linear.easeNone }), 0);

    var aboutAttribSC = new ScrollMagic.Scene({
      triggerElement: "#aboutGraphs",
      triggerHook: .7
    })
      .setTween(aboutAttribTL)
      .addTo(this.magicCtrl);

    /* Chart One - LANG */
    new ScrollMagic.Scene({
      triggerElement: "#aboutLangContain",
      triggerHook: .7
    })
      .on('start', function () {
        that.langChart();
      })
      .addTo(this.magicCtrl);
    /* Chart One - SW */
    new ScrollMagic.Scene({
      triggerElement: "#aboutSWContain",
      triggerHook: .7
    })
      .on('start', function () {
        that.SWChart();
      })
      .addTo(this.magicCtrl);
    /* Chart One - EXP */
    new ScrollMagic.Scene({
      triggerElement: "#aboutExpContain",
      triggerHook: .7
    })
      .on('start', function () {
        that.expChart();
      })
      .addTo(this.magicCtrl);

    // About Attributes Animation
    var aboutVictTL = new TimelineMax();

    var aboutVictHeader = TweenMax.from("#accompHead", 1, { y: -100, opacity: 0, ease: Linear.easeNone });
    var aboutVictBlock = TweenMax.from("#accomplish", 1, { opacity: 0, x: -100, ease: Linear.easeNone });

    aboutVictTL.add(aboutVictHeader);
    aboutVictTL.add(aboutVictBlock, 0);

    var aboutDescSC = new ScrollMagic.Scene({
      triggerElement: "#accomplish",
      triggerHook: .7
    })
      .setTween(aboutVictTL)
      .addTo(this.magicCtrl);

    // About Attributes Animation
    var aboutResumeTL = new TimelineMax();

    var aboutResHeader = TweenMax.from("#resumeHead", 1, { y: -100, opacity: 0, ease: Linear.easeNone });
    var aboutResBlock = TweenMax.from("#resume", 1, { opacity: 0, x: -100, ease: Linear.easeNone });
    var aboutResBtn = TweenMax.from("#resumeButton", 1, { opacity: 0, y: 100, ease: Linear.easeNone });

    aboutResumeTL.add(aboutResHeader);
    aboutResumeTL.add(aboutResBlock, 0);
    aboutResumeTL.add(aboutResBtn);

    var aboutDescSC = new ScrollMagic.Scene({
      triggerElement: "#resume",
      triggerHook: .7
    })
      .setTween(aboutResumeTL)
      .addTo(this.magicCtrl);
  }

  langChart() {
    var aboutLang = $("#aboutLangGraph")[0].getContext('2d');

    var langChart = new Chart(aboutLang, {
      type: 'radar',
      responsive: true,
      maintainAspectRatio: false,
      data: {
        labels: ["Python", "Java", "jQuery", "Javascript", "C#", "mySQL", "HTML", "CSS"],
        datasets: [{
          backgroundColor: 'rgba(58, 236, 164, .2)',
          data: [90, 90, 80, 85, 75, 80, 100, 90],
          borderColor: 'rgba(58, 236, 164, 1)',
          borderWidth: 5,
          lineTension: -.1,
          pointRadius: 5,
          pointBackgroundColor: 'rgba(255, 255, 255, 1)'
        }]
      },
      options: {
        animation: {
          easing: 'easeInOutBounce',
          duration: 2000
        },
        legend: {
          display: false
        },
        scale: {
          ticks: {
            backdropColor: "rgba(0, 0, 0, 0)",
            beginAtZero: true,
            max: 100,
          },
          pointLabels: {
            fontColor: "#3aa3ff",
            fontSize: 15
          }
        }
      }
    });
  }

  SWChart() {
    var aboutSW = $("#aboutSWGraph")[0].getContext('2d');

    var swChart = new Chart(aboutSW, {
      type: 'doughnut',
      data: {
        labels: ["Web Development", "App Develeopment", "Game Development", "Program Development"],
        datasets: [{
          backgroundColor: ['rgba(170, 42, 245, .2)',
            'rgba(64, 159, 239, .2)',
            'rgba(58, 236, 164, .2)',
            'rgba(64, 251, 54, .2)'],
          borderColor: ['rgba(170, 42, 245, 1)',
            'rgba(64, 159, 239, 1)',
            'rgba(58, 236, 164, 1)',
            'rgba(64, 251, 54, 1)'],
          data: [40, 20, 30, 10]
        }]
      },
      options: {
        animation: {
          easing: 'easeOutQuart',
          duration: 2000
        },
        legend: {
          labels: {
            fontColor: "black",
            fontSize: 10
          }
        }
      }
    });

  };

  expChart() {
    var aboutExp = $("#aboutExpGraph")[0].getContext('2d');

    var expChart = new Chart(aboutExp, {
      type: 'horizontalBar',
      data: {
        labels: ["xCode", "Autodesk Maya", "Unity", "Andriod Studio", "Github"],
        datasets: [{
          backgroundColor: ['rgba(238, 36, 249, .2)',
            'rgba(112, 143, 245, .2)',
            'rgba(58, 230, 238, .2)',
            'rgba(64, 237, 170, .2)',
            'rgba(70, 247, 34, .2)'],
          borderColor: ['rgba(238, 36, 249, 1)',
            'rgba(112, 143, 245, 1)',
            'rgba(58, 230, 238, 1)',
            'rgba(64, 237, 170, 1)',
            'rgba(70, 247, 34, 1)'],
          borderWidth: 3,
          data: [80, 60, 90, 70, 80]
        }]
      },
      options: {
        animation: {
          easing: 'easeOutQuart',
          duration: 2000
        },
        legend: {
          display: false
        },
        scales: {
          xAxes: [{
            ticks: {
              beginAtZero: true,
              max: 100,
              fontColor: "black",
              fontSize: 15
            }
          }],
          yAxes: [{
            ticks: {
              fontColor: "black",
              fontSize: 15
            }
          }]
        }
      }
    });
  };
}
