import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-rain',
  templateUrl: './rain.component.html',
  styleUrls: ['./rain.component.css']
})
export class RainComponent implements OnInit {
  raining: boolean;
  rainBox: any;
  rainTimer: any;
  activedrops: boolean[];
  activedropstimers: number[];
  windowHeight: number;
  windowWidth: number;

  constructor() { }

  ngOnInit() {
    this.windowHeight = $(window).height();
    this.windowWidth = $(window).width();

    this.activedrops = new Array();
    this.activedropstimers = new Array();
    for (var i = 0; i < 60; i++) {
      this.activedrops.push(false);
    }
    for (var i = 0; i < 60; i++) {
      this.activedropstimers.push(0);
    }

    this.rainTimer = setInterval(() => { this.timer(); }, 500);
  }

  // Rain animations
  drops(dropNum: number): boolean {
    return this.activedrops[dropNum - 1];
  }

  rainStart() {
    this.raining = true;
  }

  rainStop() {
    this.raining = false;
  }

  timer() {
    // Calculate scroll distance
    var dFromTop: number = $('#spacer').offset().top;
    var divSize: number = $('#landing').height();
    var percentLeft: number = 100 * (dFromTop / divSize);
    if (percentLeft <= 0) {
      percentLeft = 0;
    } else if (percentLeft >= 100) {
      percentLeft = 100;
    }
    if (percentLeft < 50) {
      this.rainStop();
    } else {
      this.rainStart();
    }

    // Make it rain
    if (this.raining == true) {
      var height;
      var x;
      var y;
      var rainDrops = Math.floor(Math.random() * 3) + 1;
      
      for (var i = 0; i <= rainDrops; i++) {
        var dropChosen = Math.floor(Math.random() * 59);
        if (this.activedrops[dropChosen] == true) {
          continue;
        }

        height = Math.random()/2 * this.windowHeight;
        x = Math.floor(Math.random() * (percentLeft / 100) * this.windowWidth) + 1;
        y = Math.floor(Math.random() * this.windowHeight) + 1;

        var divID = '.drop' + (dropChosen + 1);

        this.activedrops[dropChosen] = true;
        $(divID).css({
          "height": height,
          "top": y,
          "left": x
        });
      }
    }

    // clean up rain drops
    for (var i = 0; i < 60; i++) {
      if (this.activedrops[i] == true) {
        if (this.activedropstimers[i] == 4) {
          this.activedropstimers[i] = 0;
          this.activedrops[i] = false;
        } else {
          this.activedropstimers[i] = this.activedropstimers[i] + 1;
        }
      }
    }
  }
}
