import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {
  home: boolean;
  about: boolean;
  portfolio: boolean;
  contact: boolean;
  windowHeight: number;
  windowWidth: number;
  windowRunning: boolean;

  isCollapsed: boolean;

  ngOnInit(): void {
    this.windowHeight = $(window).height();
    this.windowWidth = $(window).width();
    this.windowRunning = false;

    console.log(this.windowHeight + " - " + this.windowWidth);

    this.home = true;
    this.about = false;
    this.portfolio = false;
    this.contact = false;

    window.onorientationchange = function () {
      var orientation = window.orientation;
      switch (orientation) {
        case 0:
        case 90:
        case -90:
          $(this).scrollTop(0);
          window.location.reload();
          break;
      }
    };
  }

  getPage(p: string) {
    this.isCollapsed = true;


    if (this.windowRunning) {
      return;
    }

    this.windowRunning = true;

    if ($('body').scrollTop() != 0) {
      $('body').animate({
        scrollTop: 0
      }, 1000);

      setTimeout(() => {
        this.setContent(p);
      }, 750);

      // Reset the animation after 2 seconds
      // if the animation is running all the way
      setTimeout(() => {
        this.windowRunning = false;
      }, 1500);
    } else {
      this.setContent(p);
      // Reset the animation after 1 second
      // if the animation is running half way
      setTimeout(() => {
        this.windowRunning = false;
      }, 750);
    }

    $('body').animate({
      scrollTop: this.windowHeight
    }, 750);
}

  setContent(p: string) {
    this.home = false;
    this.about = false;
    this.portfolio = false;
    this.contact = false;
    switch (p) {
      case "home": {
        this.home = true;
        break;
      }
      case "about": {
        this.about = true;
        break;
      }
      case "portfolio": {
        this.portfolio = true;
        break;
      }
      case "contact": {
        this.contact = true;
        break;
      }
      default: {
        console.log("ERROR");
        break;
      }
    }
  }
 

}
