import { Component, OnInit } from '@angular/core';
import { GitService } from '../../services/git.service';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { TweenMax, TimelineMax, Linear } from 'gsap';
import * as ScrollMagic from 'scrollmagic';

@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.css']
})
export class PortfolioComponent implements OnInit {
  loadingTried: boolean;
  error: boolean;
  gitInfo: any;
  projCollapse: boolean[];
  magicCtrl: any;

  constructor(private git: GitService) {
  }

  // TODO the loading screen needs to be set for waaiting on call. gitInfo == false by defalult,
  // so the buttons show up by default

  ngOnInit() {
    this.setLoadAnim();
    this.loadingTried = false;
    this.error = true;

    this.git.getGit()
      .pipe(
        catchError(err => { console.log(err); this.loadingTried = true; return throwError(err);})
      )
      .subscribe(
      data => {
        this.loadingTried = true;

        if (data["status"] == "Error") {
          this.error = true;
        } else {
          this.error = false;
          this.gitInfo = data;

          this.projCollapse = new Array();

          for (let entry of this.gitInfo) {
            this.projCollapse.push(false);
          }
          console.log(this.gitInfo);
        }
      });
  }

  setLoadAnim() {
    var that = this;

    this.magicCtrl = new ScrollMagic.Controller();

    var tl = new TimelineMax();

    tl.add(TweenMax.to("#loading", .2, { css: "opacity: 1;", ease: Linear.easeNone }));

    var scene = new ScrollMagic.Scene({
      triggerElement: "#content",
      triggerHook: 1
    })
      .setTween(tl)
      .addTo(this.magicCtrl);
  }

  loaded() {
    return this.loadingTried;
  }

  gotError() {
    return this.error;
  }

  getPics(num: number) {
    return (this.gitInfo[num])["pics"];
  }

  collapseCheck(num: number) {
    this.projCollapse[num] = !this.projCollapse[num];
    this.startGitAnim(this.projCollapse[num], (this.gitInfo[num])["title"]);
  }

  startGitAnim(open: boolean, id: string) {
    var fullId = "#" + id;

    if (open) {
      TweenMax.set(fullId, { height: "auto", paddingBottom: "3vh"})
      TweenMax.from(fullId, .75, { height: 0, paddingBottom: "0vh"})
    } else {
      TweenMax.to(fullId, .75, { height: 0, paddingBottom: "0vh", ease: Linear.easeNone });
    }
  }

  getBtnId(num: number) {
    var final = (num % 4) + 1;

    switch (final) {
      case 1:
        return "one";
      case 2:
        return "two";
      case 3:
        return "three";
      case 4:
        return "four";
    }
  }
}
