import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { FriendService } from '../../services/friend.service';
import { TweenMax, TimelineMax, Linear } from 'gsap';
import * as ScrollMagic from 'scrollmagic';
import * as $ from 'jquery';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  magicCtrl: any;

  friend = new FormGroup({
    name: new FormControl(''),
    phone: new FormControl(''),
    email: new FormControl(''),
    comment: new FormControl('')
  });

  constructor(private friendServ: FriendService) { }

  ngOnInit() {
    this.setContactAnim();
  }

  setContactAnim() {
    this.magicCtrl = new ScrollMagic.Controller();

    // Contact Content Animations
    var contacttl = new TimelineMax();
    contacttl.add(TweenMax.from("#contactHeader", 1, { opacity: 0, y: -100, ease: Linear.easeNone }), 0);
    contacttl.add(TweenMax.from("#myForm", 1, { opacity: 0, x: -100, ease: Linear.easeNone }), 0);
    contacttl.add(TweenMax.from("#mySocials", 1, { opacity: 0, x: 100, ease: Linear.easeNone }), 0);

    var contactScene = new ScrollMagic.Scene({
      triggerElement: "#mainrow",
      triggerHook: .8
    })
      .setTween(contacttl)
      .addTo(this.magicCtrl);
  }

  onSubmit() {
    this.friendServ.save(this.friend.value).subscribe(data => {
      let stat = data["status"];
      this.friendServ.sumbitSuccess(stat);
    });

    this.friend.reset;
    this.friend.patchValue({
      name: '',
      phone: '',
      email: '',
      comment: ''
    });
    $('body').animate({
      scrollTop: 0
    }, 500);
  }
}
