import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { FriendService } from '../../services/friend.service';
import { TweenMax, TimelineMax, Linear} from 'gsap';
import * as ScrollMagic from 'scrollmagic';
import * as $ from 'jquery';
import 'imports-loader?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap';


@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.css']
})
export class LandingComponent implements OnInit{
  @Output()
  pageChanged: EventEmitter<String> = new EventEmitter<String>();

  @Input()
  isCollapsed: boolean;
  magicCtrl: any;

  msg: string;
  msgAnim: any;

  constructor(private fServ:FriendService) {
  }

  ngOnInit(): void {
    this.isCollapsed = true;
    this.initAnim();
    this.setAnim();
    this.setScollListener();

    this.fServ.submitted.subscribe(s => {
      this.showMessage(s);
    });
  }

  change(evant: EventEmitter<string>, p: string) {
    this.isCollapsed = true;
    this.pageChanged.emit(p);
  }

  showMessage(s: boolean) {
    if (s) {
      this.msg = "I look forward to working with you!";
      $("#msg").css("color","#4BB543");
    } else {
      this.msg = "Something went wrong, try again";
      $("#msg").css("color", "#B33A3A");
    }
    this.msgAnim.restart();
  }

  // Permanent animation
  initAnim() {
    TweenMax.from("#landingname", 1, { opacity: 0, ease: Linear.easeNone });
    TweenMax.from("#landingwords", 1, { opacity: 0, ease: Linear.easeNone });
    TweenMax.from("#navBar", 1, { y: -25, opacity: 0, ease: Linear.easeNone });

    this.msgAnim = new TimelineMax();
    this.msgAnim.add(TweenMax.to("#msg", .5, { y: +25, opacity: 1, ease: Linear.easeNone }), "+=.5");
    this.msgAnim.add(TweenMax.to("#msg", .5, { y: -25, opacity: 0, ease: Linear.easeNone }), "+=2");
  }

  // Permanent scroll animations
  setAnim() {
    this.magicCtrl = new ScrollMagic.Controller();
    var tl = new TimelineMax();
    tl.add(TweenMax.to("#landingwords", .5, { opacity: 0, fontSize: "1wh", ease: Linear.easeNone }));
    tl.add(TweenMax.to("#navBar", .5, { opacity: 0, ease: Linear.easeNone }), "-=.5");
    tl.add(TweenMax.to(".navBtns", .5, { height: "3wh", width: "3wh", ease: Linear.easeNone }), "-=.5");

    var sceneOne = new ScrollMagic.Scene({
      triggerElement: "#spacer",
      duration: "20%",
      triggerHook: .9
    })
      .setTween(tl)
      .addTo(this.magicCtrl);

    var navAnim = TweenMax.to("nav", .5, { opacity: 1, ease: Linear.easeNone });
    var sceneTwo = new ScrollMagic.Scene({
      triggerElement: "#spacer",
      triggerHook: .2
    })
      .setTween(navAnim)
      .addTo(this.magicCtrl);
  }

  // My own scrolling animation
  setScollListener() {
    var that = this;
    $('body').scroll(function () {
      var dFromTop: number = $('#spacer').offset().top;
      var divSize: number = $('#landing').height();
      var percentLeft: number = 100 * (dFromTop / divSize);

      if (percentLeft <= 0) {
        percentLeft = 0;
      } else if (percentLeft >= 100) {
        percentLeft = 100;
      }

      that.transitionFunc(6, 4, "#landingname", "font-size", "vw", percentLeft);
      that.transitionFunc(0, 110, "#landingname", "top", "%", percentLeft);
      that.transitionFunc(1, 0, "#landingname", "opacity", "", percentLeft);
    });
  }
  transitionFunc(max, min, item, attr, scale, percent) {
    if (min <= 0) {
      var diff = Math.abs(max - min);
    }
    else {
      var diff = max - min;
    }

    var multiplier = diff / 100;

    var current = Math.round(((percent * multiplier) + min) * 100) / 100;

    var wrap = current + scale;
    $(item).css(attr, wrap);
  }

}
